import React, { Component } from 'react';
import Countdown from './Countdown.js';
import './App.css';
import Button from 'react-bootstrap/Button';

// import { Card } from 'semantic-ui-react'


class ConferenceInfo extends Component {
  handleClick(url){
    window.open(url);
  }
  render() {
    return (
      <div className="confernenceInfo">
        <div className='meta_container'>
          <h1 style={{color:"#d65a31"}} onClick={()=> {this.handleClick(this.props.meta.url)}}>{this.props.meta.conference}</h1>
          {/* <p>{this.props.meta.fullname}</p> */}
          <p>{this.props.meta.start_date} - {this.props.meta.end_date}</p>
          <p>{this.props.meta.location}</p>
        </div>
        <div className='time_container'>
          <Countdown date={new Date(this.props.meta.paper_deadline)} />
          <p style={{color:"#d65a31"}}>Paper Deadline {new Date(this.props.meta.paper_deadline).toLocaleDateString()} {new Date(this.props.meta.paper_deadline).toLocaleTimeString()}</p>
          <p>Abstract Deadline {new Date(this.props.meta.abstract_deadline).toLocaleDateString()} {new Date(this.props.meta.abstract_deadline).toLocaleTimeString()}</p>
        </div>
      </div>
    );
  }
}

class App extends Component {
  constructor(){
    super() 
      this.state = {data:require('./conferences.json')
      }
  };
  
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <h1>HCI Conference Deadlines</h1>
          <h2>Today is {new Date().toLocaleDateString()}.</h2>
        </div>
            <div>{this.state.data.map(function(item, key) {
                     return (
                        <div key={key}>
                          <ConferenceInfo meta={item} />
                        </div>
                      ) 
                   })}</div>
        </div>
    );
  }
}

export default App;



